import Mock from 'mockjs';
const LoginUsers = [
  {
    id: 1,
    username: 'admin',
    password: '123456',
    avatar: 'https://raw.githubusercontent.com/taylorchen709/markdown-images/master/vueadmin/user.png',
    name: ''
  }
];

const Users = [];

for (let i = 0; i < 86; i++) {
  Users.push(Mock.mock({
    id: Mock.Random.guid(),
    name: Mock.Random.cname(),
    addr: Mock.mock('@county(true)'),
    'age|18-60': 1,
    birth: Mock.Random.date(),
    sex: Mock.Random.integer(0, 1)
  }));
}

const TransList = [];

for(let i=0;i<20;i++) {
  TransList.push(Mock.mock({
    idTransformation: Mock.Random.guid(),
    name:Mock.Random.cname(),
    createdDate: Mock.Random.date(),
    transStatus: Mock.Random.integer(0, 2),
    description: Mock.Random.sentence(),
    created_user: LoginUsers[0].name,
    'size_rowset|18-60': 1
  }));
}

export { LoginUsers, Users, TransList};



