import axios from 'axios';
import Qs from 'qs';

let base = '';

//export const requestLogin = params => { return axios.post(`${base}/login`, params).then(res => res.data); };

export const getUserList = params => { return axios.get(`${base}/user/list`, { params: params }); };

export const getUserListPage = params => { return axios.get(`${base}/user/listpage`, { params: params }); };

export const removeUser = params => { return axios.get(`${base}/user/remove`, { params: params }); };

export const batchRemoveUser = params => { return axios.get(`${base}/user/batchremove`, { params: params }); };

export const editUser = params => { return axios.get(`${base}/user/edit`, { params: params }); };

export const addUser = params => { return axios.get(`${base}/user/add`, { params: params }); };

/**/
export const getTransList = params => {return axios.get(`/trans`, { params: params }); };
/**/
export const getJobList = params => { return axios.get(`/jobs`, { params: params }); };
/**/
export const requestLogin = params => { return axios.get(`/login`, { params: params}).then(res => res).catch(error => error.response)};
/**/
export const updateJob = params => { return axios.post(`/jobs/type/u`, Qs.stringify(params)); };
/**/
export const removeJob = params => { return axios.post(`/jobs/type/d`, Qs.stringify(params)); };
/**/
export const runJob = params => { return axios.get(`/jobs/run/`+params.idJob); };
/**/
export const stopJob = params => { return axios.get(`/jobs/stop/`+params.idJob); };

//http request 
axios.interceptors.request.use(
    config => {
        let result = localStorage.getItem('result');
        if (result) {  // tokenhttp headertoken
            config.headers['Authorization'] = result;
        }
        return config;
    },
    err => {
        return Promise.reject(err);
    }
);



