<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="queryForm" ref="queryForm">
				<el-form-item label="">
					<el-input placeholder="" v-model="queryform.name"></el-input>
				</el-form-item>
				<el-form-item label="">
					<div class="block">
						<el-date-picker type="daterange" placeholder="" v-model="queryform.createdDate"></el-date-picker>
					</div>
				</el-form-item>
				<el-form-item label="">
					<el-select v-model="queryform.jobStatus" placeholder="">
						<el-option label="" value="0"></el-option>
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers"></el-button>
					<el-button @click="resetForm()"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--  -->
		<el-table :data="jobslist" border highlight-current-row v-loading="loading" style="width: 100%;">
			<el-table-column align="center" prop="idJob" label="" width="160" sortable>
			</el-table-column>
			<el-table-column prop="name" align="center" label="" width="160" sortable>
			</el-table-column>
			<el-table-column prop="createdDate" align="center" label="" width="160" sortable>
			</el-table-column>
			<el-table-column prop="jobStatus" align="center" label="" width="160" :formatter="formatJobsState" sortable>
			</el-table-column>
			<el-table-column label="" align="center">
				<template scope="scope">
					<el-button class="opsbtn" v-if="scope.row.jobStatus==0" size="mini" @click="handleStart(scope.$index, scope.row)" ><i class="el-icon-caret-right el-icon--left"></i></el-button>
					<el-button type="danger" v-else size="mini" @click="handleStop(scope.$index, scope.row)"><i class="el-icon-caret-right el-icon--left"></i></el-button>
					<el-button type="info" size="mini" @click="handleView(scope.$index, scope.row)"><i class="el-icon-view el-icon--left"></i></el-button>
					<!--<el-button class="opsbtn" size="mini" @click="handleAdd(scope.$index, scope.row)"><i class="el-icon-view el-icon&#45;&#45;left"></i></el-button>-->
					<el-button type="info" size="mini" @click="handleEdit(scope.$index, scope.row)"><i class="el-icon-edit el-icon--left"></i></el-button>
					<el-button type="danger" size="mini" @click="handleDel(scope.$index, scope.row)"><i class="el-icon-delete el-icon--left"></i></el-button>
				</template>
			</el-table-column>
		</el-table>
		<!---->
		<el-col :span="24" class="toolbar pagebar">
			<el-pagination class="pageitem" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentpage" :page-sizes="showpages" :page-size="pagesize" layout="total, sizes, prev, pager, next, jumper" :total="totalpage"></el-pagination>
		</el-col>
		<!--dialog    -->
		<el-dialog :title="dialogtitle" v-model="dialongVisible" :close-on-click-modal="false">
			<el-form :model="dialogform" label-width="150px" ref="dialogform" :rules="rules">
				<el-form-item prop="idJob" style="display:none;">
					<el-input v-model="dialogform.idJob"></el-input>
				</el-form-item>
				<el-form-item label="idDirectory" prop="idDirectory">
					<el-input v-model="dialogform.idDirectory" placeholder="idDirectory" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="dialogform.name" placeholder="" auto-complete="off" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="description" prop="description">
					<el-input v-model="dialogform.description" type="textarea" placeholder="description" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="extendedDescription" prop="extendedDescription">
					<el-input v-model="dialogform.extendedDescription" type="textarea" placeholder="extendedDescription" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="jobVersion" prop="jobVersion">
					<el-input v-model="dialogform.jobVersion" placeholder="jobVersion" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="jobRunrule" prop="jobRunrule">
					<el-input v-model="dialogform.jobRunrule" placeholder="jobRunrule" :disabled="isdisable"></el-input>
				</el-form-item>
				<el-form-item label="jobStatus" prop="jobStatus">
					<el-select v-model="queryform.jobStatus" placeholder="" :disabled="true">
						<el-option label="" value="0"></el-option>
						<el-option label="" value="1"></el-option>
						<el-option label="" value="2"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="idDatabaseLog" prop="idDatabaseLog">
					<el-input v-model="dialogform.idDatabaseLog" placeholder="idDatabaseLog" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="tableNameLog" prop="tableNameLog">
					<el-input v-model="dialogform.tableNameLog" placeholder="tableNameLog" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="createdUser" prop="createdUser">
					<el-input v-model="dialogform.createdUser" placeholder="createdUser" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="" prop="createdDate">
					<el-date-picker v-model="dialogform.createdDate" type="datetime" placeholder="" :disabled="true"></el-date-picker>
				</el-form-item>
				<el-form-item label="modifiedUser" prop="modifiedUser">
					<el-input v-model="dialogform.modifiedUser" placeholder="modifiedUser" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="modifiedDate" prop="modifiedDate">
					<el-date-picker v-model="dialogform.modifiedDate" type="datetime" placeholder="" :disabled="true"></el-date-picker>
				</el-form-item>
				<el-form-item label="useBatchId" prop="useBatchId">
					<el-input v-model="dialogform.useBatchId" placeholder="useBatchId" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="passBatchId" prop="passBatchId">
					<el-input v-model="dialogform.passBatchId" placeholder="passBatchId" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="useLogfield" prop="useLogfield">
					<el-input v-model="dialogform.useLogfield" placeholder="useLogfield" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="sharedFile" prop="sharedFile">
					<el-input v-model="dialogform.sharedFile" placeholder="sharedFile" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="" prop="RUNSTATUS">
					<el-select v-model="queryform.RUNSTATUS" placeholder="" :disabled="true">
						<el-option label="" value="0"></el-option>
						<el-option label="" value="1"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="LASTUPDATE" prop="LASTUPDATE">
					<el-date-picker v-model="dialogform.LASTUPDATE" type="datetime" placeholder="" :disabled="true"></el-date-picker>
				</el-form-item>
				<el-form-item label="AUTORESTARTNUM" prop="AUTORESTARTNUM">
					<el-input v-model="dialogform.AUTORESTARTNUM" placeholder="AUTORESTARTNUM" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="REPOSITORYCODE" prop="REPOSITORYCODE">
					<el-input v-model="dialogform.REPOSITORYCODE" placeholder="REPOSITORYCODE" :disabled="true"></el-input>
				</el-form-item>
				<el-form-item label="PROJECTCODE" prop="PROJECTCODE">
					<el-input v-model="dialogform.PROJECTCODE" placeholder="PROJECTCODE" :disabled="true"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="dialongVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading" v-show="iscommitbtnshow"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util';
	//import axios from 'axios';
	//import NProgress from 'nprogress'
	import { getJobList,removeJob,runJob,stopJob,updateJob} from '../../api/api';

	export default {
		data() {
		return {
			jobslist: [],
			queryform: {
				idJob: '',
				name: '',
				createdDate: '',
				jobStatus: ''
			},
			rules: {
				jobRunrule: [
					{ required: true, message: '', trigger: 'blur' }
				]
			},
			dialogform: {
				idJob: '',
				idDirectory: '',
				name: '',
				description: '',
				extendedDescription: '',
				jobVersion: '',
				jobRunrule:'',
				jobStatus: '',
				idDatabaseLog: '',
				tableNameLog: '',
				createdUser: '',
				createdDate: '',
				modifiedUser: '',
				modifiedDate: '',
				useBatchId: '',
				passBatchId: '',
				useLogfield: '',
				sharedFile: '',
				RUNSTATUS: '',
				LASTUPDATE: '',
				AUTORESTARTNUM: '',
				REPOSITORYCODE: '',
				PROJECTCODE: ''
			},
			loading: false,
			currentpage: 1,
			totalpage: 0,
			pagesize: 5,
			showpages: [5,10,15,20],
			dialongVisible: false,
			dialogtitle: '',
			isdisable: false
		}
	},
	methods: {
		handleDel : function(index, row) {
			this.$confirm('?', '', {
				type: 'warning'
			}).then(() => {
				this.listLoading = true;
				let para = { idJob: row.idJob };
				removeJob(para).then((res) => {
					debugger
					this.listLoading = false;
					if(res.data.status==0) {
						this.$message({
							message: res.data.message,
							type: 'success'
						});
						this.getJobList();
					} else {
						this.$message.error('res.data.message');
					}
				});
			});
		},
		handleView: function(index, row) {
			debugger
			this.iscommitbtnshow = false;
			this.isdisable = true;
			this.dialongVisible = true;
			this.dialogtitle = '';
			this.dialogform = Object.assign({}, this.jobslist[index]);
		},
		handleStart :function(index, row) {
			debugger
			this.dialogform = Object.assign({}, this.jobslist[index]);
			if(!this.dialogform.jobRunrule) {
				this.$message({
					message: '!',
					type: 'warning'
				});
			} else {
				let para = {idJob: this.dialogform.idJob};
				runJob(para).then((res) => {
					this.listLoading = false;
					if (res.data.status == 0) {
						this.$message({
							message: res.data.message,
							type: 'success'
						});
						this.getJobList();
					} else {
						this.$message.error('res.data.message');
					}
				});
			}
		},
		addSubmit: function() {
			debugger
			this.listLoading = true;
			this.$refs.dialogform.validate((valid) => {
				if (valid) {
					 let para = {idJob: this.dialogform.idJob,jobRunrule: this.dialogform.jobRunrule};
					updateJob(para).then((res) => {
						 this.listLoading = false;
						 if(res.data.status==0) {
							 this.$message({
							 message: res.data.message,
							 type: 'success'
							 });
							 this.getJobList();
							 this.dialongVisible = false;
						 } else {
						 	this.$message.error('res.data.message');
						 }
					 });
				} else {
					return false;
				}
			});
		},
		handleStop: function(index, row) {
			this.listLoading = true;
			let para = { idJob: row.idJob };
			stopJob(para).then((res) => {
				this.listLoading = false;
				if(res.data.status==0) {
					this.$message({
						message: res.data.message,
						type: 'success'
					});
					this.getJobList();
				} else {
					this.$message.error('res.data.message');
				}
			});
		},
		handleEdit: function(index, row) {
			this.iscommitbtnshow = true;
			this.dialongVisible = true;
			this.dialogtitle = '';
			this.isdisable = false;
			this.dialogform = Object.assign({}, this.jobslist[index]);
		},
		formatJobsState: function (row, column) {
			return row.jobStatus == 0 ? '' : row.jobStatus == 1 ? '' : '';
		},
		getJobList: function() {
			let para = {
				currentPage: this.currentpage,
				pageNumber: this.pagesize
			};
			this.loading = true;
			getJobList(para).then((res) => {
				debugger
				this.jobslist = res.data.data;
				this.totalpage = res.data.page.totalNumber;
				this.loading = false;
		});
	},
	handleCurrentChange: function (val) {
		this.currentpage = val;
		this.getJobList();
	},
	handleSizeChange: function(val) {
		this.pagesize = val;
		this.getJobList();
	},
	resetForm: function() {
		for(let name in this.queryform) {
			this.queryform[name] ='';
		}
	}
	},
	mounted() {
		this.getJobList();
	}
	}
</script>

<style scoped lang="scss">
	@import '~scss_vars';
	.opsbtn {
		background-color: #18a689;
		border-color: #18a689;
		color: #FFFFFF;
	}
	.pagebar {
		background-color: #fff;
	.pageitem {
		float: right;
	}
	}
</style>


